ght: 0;\n    left: 0;\n    bottom: 0;\n    width: 100vw;\n    height: 100vh;\n    display: flex;\n    justify-content: center;\n    align-items: flex-start;\n    background: rgba(0,0,0,.5);\n    opacity: 0;\n    transition: opacity .3s;\n    overflow: auto;\n    &, & *{\n        pointer-events: none;\n        box-sizing: border-box;\n        font-family: Roboto, sans-serif;\n        user-select: none;\n        &::-webkit-scrollbar{\n            width: 2px;\n            background: transparent;\n            border-radius: 10px;\n        }\n        &::-webkit-scrollbar-thumb{\n            width: 2px;\n            background: #18538E;\n            border-radius: 10px;\n        }\n    }\n    & ol, & ul, & li{\n        padding: 0;\n        list-style: none;\n    }\n    & button, & button:active, & button:focus,\n    & input, & input:active, & input:focus,\n    & textarea, & textarea:active, & textarea:focus{\n        border:none;\n        outline:none;\n        resize: none;\n    }\n    & a, & a:hover, & a:focus{\n        text-decoration:none;\n        color:inherit;\n        outline:none;\n    }\n    & h1,& h2,& h3,& h4,\n    & h5,& h6,& p,& label{\n        margin: 0;\n        padding: 0;\n    }\n    & input[type=\"number\"]::-webkit-outer-spin-button,\n    & input[type=\"number\"]::-webkit-inner-spin-button {\n        -webkit-appearance: none;\n        margin: 0;\n    }\n\n    & #appointment-form{\n        width: 100%;\n        max-width: 600px;\n        margin-top: 30px;\n        padding: 20px 10px;\n        display: flex;\n        flex-direction: column;\n        align-items: center;\n        background: #025ea1;\n        box-shadow: 0 0 5px 1px #025ea1;\n        border-radius: 6px;\n        transition: .5s;\n        opacity: 0;\n        transform: translateY(-110vh);\n        transform-origin: left bottom;\n        z-index: 1000;\n        overflow: hidden;\n        &.off > *:not(#appointment-result-block){\n            opacity: 0;\n            pointer-events: none;\n        }\n        & ::placeholder{\n            color: #f5f5f5;\n        }\n    }\n\n    &.active{\n        opacity: 1;\n        &, & * {\n            pointer-events: auto;\n        }\n        & #appointment-form{\n            transform: translateY(0);\n            opacity: 1;\n        }\n    }\n\n    & .selection-block,\n    & .appointment-form_input-wrapper{\n        display: block;\n        position: relative;\n        width: 100%;\n        max-height: 300px;\n        margin-bottom: 15px;\n        border-radius: 6px;\n        cursor: pointer;\n        background-color: #1B3257;\n        transition: .5s;\n        &.error{\n            box-shadow: inset 0 0 0 2px red;\n        }\n        &.hidden{\n            max-height: 0;\n            margin-bottom: 0;\n            transform-origin: center top;\n            transform: scaleY(0);\n        }\n    }\n    &  .selection-item{\n        &-selected{\n            position: relative;\n            width: 100%;\n            height: 36px;\n            display: flex;\n            justify-content: flex-start;\n            align-items: center;\n            padding: 0 25px 0 15px;\n            color: #FFF;\n            font-size: 12px;\n            transition: .3s;\n            border-radius: 6px;\n            &:hover{\n                background-color: #254579;\n            }\n            &:before{\n                content:'';\n                position: absolute;\n                top: 50%;\n                right: 20px;\n                transform: translateY(-50%);\n                width: 10px;\n                height: 10px;\n                background: url('../static/img/dropdown.svg') center center/contain no-repeat;\n                cursor: pointer;\n            }\n            &  span{\n                white-space: nowrap;\n                overflow: hidden;\n                text-overflow: ellipsis;\n            }\n        }\n        &-list{\n            display:flex;\n            justify-content: flex-start;\n            al